.aboutSection {
  padding: 4rem 2rem;
  background-color: #f9f9f9;
}

.aboutContainer {
  max-width: 1200px;
  margin: 0 auto;
}

.aboutHeader {
  text-align: center;
  margin-bottom: 3rem;
}

.aboutTitle {
  font-size: 2.5rem;
  color: #2c3e50;
  margin-bottom: 0.5rem;
}

.tagline {
  font-size: 1.2rem;
  color: #e74c3c;
  font-weight: 500;
}

.missionSection {
  margin-bottom: 3rem;
  text-align: center;
}

.sectionTitle {
  color: #2c3e50;
  margin-bottom: 1rem;
}

.missionText {
  font-size: 1.1rem;
  line-height: 1.6;
  color: #555;
  max-width: 800px;
  margin: 0 auto;
}

.impactSection {
  margin: 4rem 0;
}

.impactStats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1.5rem;
  text-align: center;
}

.statCard {
  background: white;
  padding: 1.5rem;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.statNumber {
  font-size: 2rem;
  color: #e74c3c;
  margin-bottom: 0.5rem;
}

.statLabel {
  color: #7f8c8d;
}

.educationSection {
  margin: 4rem 0;
}

.educationPoints {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
}

.pointCard {
  background: white;
  padding: 1.5rem;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.pointTitle {
  color: #3498db;
  margin-bottom: 1rem;
}

.pointText {
  color: #555;
  line-height: 1.6;
}

.testimonialsSection {
  margin: 4rem 0;
}

.testimonialCards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
}

.testimonial {
  background: white;
  padding: 1.5rem;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  position: relative;
}

.testimonialText {
  color: #555;
  line-height: 1.6;
  font-style: italic;
  margin-bottom: 1rem;
}

.testimonialAuthor {
  font-weight: 600;
  color: #2c3e50;
  text-align: right;
}

/* Responsive Design */
@media (max-width: 768px) {
  .aboutTitle {
    font-size: 2rem;
  }
  
  .impactStats,
  .educationPoints,
  .testimonialCards {
    grid-template-columns: 1fr;
  }
}