.adminContainer {
  padding: 2rem;
}

.adminView h2 {
  color: var(--dark-color);
  margin-bottom: 2rem;
  text-align: center;
  font-size: 1.8rem;
}

.adminView table {
  width: 100%;
  border-collapse: collapse;
  background-color: white;
  box-shadow: var(--shadow);
  border-radius: 8px;
  overflow: hidden;
}

.adminView th,
.adminView td {
  padding: 1rem;
  text-align: left;
  border-bottom: 1px solid #eee;
}

.adminView th {
  background-color: var(--dark-color);
  color: white;
  font-weight: 500;
}

.adminView tr:hover {
  background-color: #f9f9f9;
}

.adminView tr:nth-child(even) {
  background-color: #f5f5f5;
}

.adminView tr:nth-child(even):hover {
  background-color: #f0f0f0;
}

.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
  font-size: 1.2rem;
  color: var(--text-light);
}

@media (max-width: 768px) {
  .adminContainer {
    padding: 1rem;
    overflow-x: auto;
  }
  
  .adminView table {
    display: block;
    width: 100%;
    overflow-x: auto;
    white-space: nowrap;
  }
}