.home {
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url('https://rajfoundation.co.in/wp-content/uploads/2021/03/IMG-35281-scaled.jpg');
  background-size: cover;
  background-position: center;
  color: white;
  padding: 4rem 2rem;
}

.home h1 {
  font-size: 3rem;
  margin-bottom: 1.5rem;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.home p {
  font-size: 1.2rem;
  max-width: 700px;
  margin-bottom: 2.5rem;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
}

.ctaButtons {
  display: flex;
  gap: 5rem;
}

@media (max-width: 768px) {
  .home h1 {
    font-size: 2.2rem;
  }
  
  .home p {
    font-size: 1rem;
  }
  
  .cta-buttons {
    flex-direction: column;
    width: 100%;
  }
  
  .cta-buttons .button {
    width: 100%;
  }
}